body {
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.people {
  grid-template-columns: repeat(2, 200px);
  gap: 30px;
  display: grid;
}

.person {
  text-align: center;
  width: 200px;
  height: 231px;
  padding: 10px;
  transition: all 1s;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px 0 0;
  width: 264px;
  height: 344.4px;
}

.person:hover .photo {
  border-radius: 60px;
  width: 240px;
  height: 240px;
}

.person:hover .image {
  object-fit: cover;
  border-radius: 60px;
  width: 240px;
  height: 240px;
}

.person .photo {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: all 1s;
  position: relative;
}

.person .photo .image {
  object-fit: cover;
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: all 1s;
}

.person .photo .rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 85px;
  left: 155px;
}

.person .details {
  margin-top: 10px;
}

.person .details .name {
  color: #333;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
  text-decoration: none;
  display: block;
}

.person .details .extra {
  display: none;
}

@media (width <= 750px) {
  .people {
    padding: 0 50px;
  }
}

@media (width >= 1500px) {
  .people {
    padding: 0 50px;
  }
}
